main {
  width: 320px;
  background: #fff;
  padding: 30px;
  box-shadow: 0 0 15px 6px #00000021;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
h3 {
  font-size: 24px;
  margin-top: 0;
  margin-bottom: 35px;
  color: #e91e63;
}
.person {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.person .info {
  margin-left: 10px;
}
.person p,
.person h4 {
  margin: 0;
  margin-bottom: 10px;
}
.person img {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  box-shadow: 0 4px 7px 0px #00000057;
}
button {
  background: #e91e63;
  border: none;
  color: #fff;
  padding: 14px 20px;
  border-radius: 5px;
  font-size: 20px;
  width: 100%;
  margin-top: 20px;
  cursor: pointer;
  transition: 0.2s;
  font-weight: 600;
}
button:hover {
  background-color: #d11d59;
}
